<template>
  <div class="dashboard">
    <h1>{{ msg }}</h1>
    <h1 v-if="userMiners.length > 0">Your miners:</h1>
    <div v-else>
      <h1 >You have no miners</h1>
    </div>
    <b-card-group deck
                  class="mb-3">
      <b-card
        v-for="miner in userMiners" :key="miner.id"
        :title="miner.name"
        :img-src="miner.image"
        img-alt="image"
        img-top
        style="max-width: 20rem;"
        class="mb-2">
        <b-card-body>
          <p>Power: {{miner.power}}</p>
          <p>Start Time: {{miner.startTime}}</p>
          <p>Upgrades: {{miner.upgrades}}</p>
        </b-card-body>
        <b-button variant="primary" v-on:click="sell(miner.id)" v-b-modal.sellMinerModal>Sell</b-button>
      </b-card>
    </b-card-group>

    <b-modal id="sellMinerModal" title="Selling miner" @ok="handleSell">
      <b-form-group horizontal
                    label="Price ETH:"
                    label-class="text-sm-right"
                    label-for="minerPrice">
        <b-form-input id="minerPrice"
                      type="number"
                      v-model="sellingMiner.price">
        </b-form-input>
      </b-form-group>
    </b-modal>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import CryptoMiner from '@/js/roller'

  export default {
    name: 'dashboard',
    data () {
      return {
        msg: 'Welcome to the roller dapp',
        userMiners: [],
        sellingMiner: {
          id: null,
          price: null
        }
      }
    },
    computed: mapState({
      contract: state => state.contract,
      user: state => state.user
    }),

    beforeMount: function () {
      CryptoMiner.instance.balanceOf(this.user.address)
        .then(count => {
          for (let i = 0; i < count; i++) {
            CryptoMiner.instance.tokenOfOwnerByIndex(this.user.address, i)
              .then(minerId => {
                Promise.all([
                  CryptoMiner.instance.miners(minerId),
                  CryptoMiner.instance.tokenURI(minerId)
                ])
                  .then(it => {
                    let miner = it[0]
                    let url = it[1]
                    this.userMiners.push({
                      id: Number(minerId),
                      name: miner[0],
                      power: miner[1],
                      startTime: miner[2],
                      upgrades: miner[3],
                      image: url
                    })
                  })
              })
          }
        })
    },
    methods: {
      sell: function (id) {
        this.sellingMiner.id = id
        console.log('Got selling id', this.sellingMiner.id)
      },
      handleSell: function (event) {
        if (this.sellingMiner.price == null || this.sellingMiner.price <= 0) {
          alert('Enter valid price')
        }
        console.log('User is selling ', this.sellingMiner)
        CryptoMiner.instance.createLot(this.sellingMiner.id, window.web3.toWei(this.sellingMiner.price, 'ether'))
          .then(receipt => {
            console.log('Lot created', receipt)
          })

        this.sellingMiner.id = null
        this.sellingMiner.price = null
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
    display: block;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



