import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewMiners from '@/components/NewMiners'
import OldMiners from '@/components/OldMiners'
import AdminPanel from '@/components/AdminPanel'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'newMiners',
      component: NewMiners
    },
    {
      path: '/old',
      name: 'oldMiners',
      component: OldMiners
    },
    {
      path: '/admin',
      name: 'adminPanel',
      component: AdminPanel
    }
  ]
})



