<template>
  <div>
    <b-card-group deck
                  class="mb-3">
      <b-card
        v-for="lot in data" :key="lot.id"
        :title="lot.name"
        :img-src="lot.image"
        img-alt="image"
        img-top
        style="max-width: 20rem;"
        class="mb-2">
        <b-card-body>
          <p>Power: {{lot.power}}</p>
          <h3>Price: {{lot.price}} eth</h3>
        </b-card-body>
        <b-button variant="primary" v-on:click="buy(lot)">Buy it!</b-button>
        <b-card-footer class="mt-2"> Lot info: <br/>
          Old price: {{lot.oldPrice}} eth <br/>
          created at {{lot.created}} <br/>
          updated at {{lot.updated}}
        </b-card-footer>
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
  import CryptoMiner from '@/js/roller'
  import {mapState} from 'vuex'

  export default {
    name: 'newMiners',
    data () {
      return {
        data: []
      }
    },
    computed: mapState({
      contract: state => state.contract
    }),
    beforeMount: function () {
      CryptoMiner.instance.getMinerStock(this.contract.owner)
        .then(data => {
          let owners = data[0]
          let ids = data[1]
          let prices = data[2]
          let creates = data[3]
          let updates = data[4]
          let oldPrices = data[5]

          for (let i = 0; i < ids.length; i++) {
            Promise.all([
              CryptoMiner.instance.miners(ids[i]),
              CryptoMiner.instance.tokenURI(ids[i])
            ])
              .then(it => {
                let miner = it[0]
                let url = it[1]
                this.data.push({
                  id: Number(ids[i]),
                  owner: owners[i],
                  price: window.web3.fromWei(prices[i], 'ether'),
                  created: creates[i],
                  updated: updates[i],
                  oldPrice: oldPrices[i],
                  name: miner[0],
                  power: miner[1],
                  startTime: miner[2],
                  upgrades: miner[3],
                  image: url
                })
              })
          }
        })
    },
    methods: {
      buy: function (lot) {
        CryptoMiner.instance.buyNew(lot.id, {
          value: window.web3.toWei(lot.price, 'ether')
        })
      }
    }
  }

</script>



