import Vue from 'vue'
import Vuex from 'vuex'
import CryptoMiner from '@/js/roller'

Vue.use(Vuex)

// initial state
const state = {
  admin: {
    totalSupply: -1,
    ownerCutPercentage: -1,
    lotUpdateInterval: -1

  },
  contract: {
    owner: ''
  },
  user: {
    address: ''
  }
}

// getters
const getters = {
  isOwner: state => {
    return state.contract.owner === state.user.address
  },
  contractLoaded: state => {
    return state.contract.owner !== ''
  }
}

// actions
const actions = {
  loadContract (context) {
    CryptoMiner.init()
      .then(() => {
        CryptoMiner.instance.owner()
          .then(owner => {
            console.log('Got owner address', owner)
            context.commit({
              type: 'setContractOwner',
              address: owner
            })
          })
      })
  },
  loadUser (context) {
    window.web3.eth.getCoinbase(function (err, account) {
      if (err === null) {
        context.commit({
          type: 'setUserAddress',
          address: account
        })
      }
    })
  },
  loadAdminInfo (context) {
    Promise.all([
      CryptoMiner.instance.totalSupply(),
      CryptoMiner.instance.ownerCutPercentage(),
      CryptoMiner.instance.lotUpdateInterval()
    ])
      .then(data => {
        context.commit({
          type: 'setTotalSupply',
          value: data[0]
        })
        context.commit({
          type: 'setOwnerCutPercentage',
          value: data[1]
        })
        context.commit({
          type: 'setLotUpdateInterval',
          value: data[2]
        })
      })
  }
}

// mutations
const mutations = {
  setContractOwner (state, payload) {
    state.contract.owner = payload.address
  },
  setUserAddress (state, payload) {
    state.user.address = payload.address
  },
  setTotalSupply (state, payload) {
    state.admin.totalSupply = payload.value
  },
  setOwnerCutPercentage (state, payload) {
    state.admin.ownerCutPercentage = payload.value
  },
  setLotUpdateInterval (state, payload) {
    state.admin.lotUpdateInterval = payload.value
  }

}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



