<template>
  <div>
    <div>
      <h1>Statistics: </h1>
      <h4>Total supply of miners in the whole system: {{Number(adminInfo.totalSupply)}}</h4>
    </div>

    <div class="mt-5">
      <h1>Administrative operations: </h1>

      <b-card bg-variant="light" class="mt-3">
        <b-form-group horizontal
                      breakpoint="lg"
                      label="Create New Miner:"
                      label-size="lg"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">
          <b-form-group horizontal
                        label="Miner name:"
                        label-class="text-sm-right"
                        label-for="minerName">
            <b-form-input id="minerName"
                          v-model="newMiner.name"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        label="Power:"
                        label-class="text-sm-right"
                        label-for="minerPower">
            <b-form-input id="minerPower"
                          type="number"
                          v-model="newMiner.power"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        label="Price ETH:"
                        label-class="text-sm-right"
                        label-for="minerPrice">
            <b-form-input id="minerPrice"
                          type="number"
                          v-model="newMiner.price">
            </b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        label="Image url:"
                        label-class="text-sm-right"
                        label-for="minerUrl">
            <b-form-input id="minerUrl"
                          v-model="newMiner.url">
            </b-form-input>
          </b-form-group>
        </b-form-group>
        <b-button variant="primary" v-on:click="createMiner">Create</b-button>
      </b-card>

      <div class="mt-3">
        <b-row>
          <b-col lg="6">
            <h4 class="mt-1">Current Owner cut percent = {{Number(adminInfo.ownerCutPercentage)}} %</h4>
          </b-col>
          <b-col lg="6">
            <b-input-group prepend="New owner cut percent:">
              <b-form-input type="number"
                            v-model="newCutPercent">
              </b-form-input>
              <b-input-group-append>
                <b-button variant="primary" v-on:click="updateCutPercent">Update</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-col>
        </b-row>
      </div>

      <div class="mt-3">
        <b-row>
          <b-col lg="6">
            <h4 class="mt-1">Current Lot Update interval = {{Number(adminInfo.lotUpdateInterval)}} blocks</h4>
          </b-col>
          <b-col lg="6">
            <b-input-group prepend="New Lot Update Interval:">
              <b-form-input type="number"
                            v-model="newLotUpdateInterval">
              </b-form-input>
              <b-input-group-append>
                <b-button variant="primary" v-on:click="updateLotUpdateInterval">Update</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-col>
        </b-row>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import CryptoMiner from '@/js/roller'

  export default {
    name: 'adminPanel',
    data () {
      return {
        newMiner: {
          name: '',
          power: null,
          price: null,
          url: ''
        },
        newLotUpdateInterval: null,
        newCutPercent: null
      }
    },
    computed: mapState({
      adminInfo: state => state.admin
    }),

    beforeMount: function () {
      this.$store.dispatch('loadAdminInfo')
    },
    methods: {
      createMiner: function () {
        CryptoMiner.instance.mint(
          this.newMiner.name,
          this.newMiner.power,
          window.web3.toWei(this.newMiner.price, 'ether'),
          this.newMiner.url)
          .then(receipt => {
            console.log(receipt)
          })
      },
      updateCutPercent: function () {
        CryptoMiner.instance.setOwnerCut(this.newCutPercent)
          .then(receipt => {
            console.log(receipt)
          })
      },
      updateLotUpdateInterval: function () {
        CryptoMiner.instance.setUpdateInterval(this.lotUpdateInterval)
          .then(receipt => {
            console.log(receipt)
          })
      }
    }
  }
</script>



